* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

p {
  text-align: center;
}

.drum-pad {
  background-color:#3A86FF;
  width: 90%;
  font-size: 3em;
  text-align: center;
  border: none;
  margin: 5% 5% 0;
  padding: 5%;
  cursor: pointer;
  outline: none;
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-column-gap: 1px;
  grid-row-gap: 1px;
  width: 90%;
  margin-left: 5%;
}
